const { daysChartBuilder } = require('./charts.js') //    

module.exports.threeDayForecastBuilder = (async () => {
  try {
    let response = await fetch('https://services.swpc.noaa.gov/text/3-day-forecast.txt') //   
    let textForecast = await response.text() //    
    let resultForecast = threeDayTablesFiller(textForecast) //  

    daysChartBuilder(resultForecast) //  
  } catch (error) {
    console.error(`    : ${error}`)
  }
})()

function threeDayTablesFiller(data) {
  const startOfForecastTable = new RegExp('(00-03UT)', 'g') //     KP-
  const endOfForecastTable = new RegExp('\n\nRationale', 'g') //   
  const extraSignatures = new RegExp('[(]G[1-5][)]', 'g') //   ,  (G1).      ,  -      response.text(),  

  const indexOfStart = data.search(startOfForecastTable)
  const indexOfEnd = data.search(endOfForecastTable)
  let crudeForecast = data.slice(indexOfStart, indexOfEnd) //      

  if (crudeForecast.includes('G')) {
    crudeForecast = crudeForecast.replaceAll(extraSignatures, '    ') //  ,     
  }

  const resultForecast = crudeForecast
    .split(' ') //      
    .filter(item => item !== '')

  const firstDayChartData = new Array()
  const secondDayChartData = new Array()
  const thirdDayChartData = new Array()

  for (let i = 1; i < 30; i += 4) {
    firstDayChartData.push(Number(resultForecast[i]))
  }

  for (let j = 2; j < 31; j += 4) {
    secondDayChartData.push(Number(resultForecast[j]))
  }

  for (let c = 3; c < 32; c += 4) {
    thirdDayChartData.push(Number(resultForecast[c]))
  }

  return { firstDayChartData, secondDayChartData, thirdDayChartData } //    
}
